import { graphql } from 'gatsby'
import React from 'react'
import Layout from '../components/Layout.js'
import SectionBody from '../components/SectionBody.js'
import SectionHeader from '../components/SectionHeader.js'

const Index = (props) => {
  return (
    <Layout page={props.data.page} settings={props.data.contentSettings}>
      {/*
      ============================
      === #section-default ===
      ============================
      */}
      <section id="section-default" className="section section-default">
        <div className="container">
          <SectionHeader pageTitle={props.data.page.frontmatter.title} pageDescription={props.data.page.frontmatter.description}></SectionHeader>
          <SectionBody>
            <div className="columns">
              <div className="column">
                <div
                  className="content has-text-centered"
                  dangerouslySetInnerHTML={{
                    __html: `
                      ${props.data.page.html || 'ðŸ¤”'}
                    `
                  }}>
                </div>
              </div>
            </div>
          </SectionBody>
        </div>
      </section>
    </Layout>
  )
}

export const query = graphql`
  query($id: String = "") {
    page: markdownRemark(id: {eq: $id}) {
      ...ContentCommons
      ...ContentPage
    }
    contentSettings: markdownRemark(frontmatter: {collectionKey: {eq: "settings"}}) {
      ...ContentCommons
      ...ContentSettings
    }
  }
`

export default Index
